.cta {
  color: #fff;
  background: #050221;
  padding: 10px 25px;
  font-family: Aileron;
  font-size: 20px;
  text-decoration: none;
  transition: all 1s;
  display: flex;
  transform: skewX(-15deg);
  box-shadow: 6px 6px #fffdfd4d;
}

.svg_home {
  margin-top: 1px;
  margin-left: -10px;
}

.cta:focus {
  outline: none;
}

.cta:hover {
  transition: all .5s;
  box-shadow: 10px 10px #006e48;
}

.cta span:nth-child(2) {
  margin-right: 0;
  transition: all .5s;
}

.cta:hover span:nth-child(2) {
  margin-right: 45px;
  transition: all .5s;
}

span {
  transform: skewX(15deg);
}

span:nth-child(2) {
  width: 20px;
  margin-left: 30px;
  position: relative;
  top: 12%;
}

path.one {
  transition: all .4s;
  transform: translateX(-60%);
}

path.two {
  transition: all .5s;
  transform: translateX(-30%);
}

.cta:hover path.three {
  animation: 1s .2s infinite color_anim;
}

.cta:hover path.one {
  animation: 1s .6s infinite color_anim;
  transform: translateX(0%);
}

.cta:hover path.two {
  animation: 1s .4s infinite color_anim;
  transform: translateX(0%);
}

@keyframes color_anim {
  0% {
    fill: #fff;
  }

  50% {
    fill: #006e48;
  }

  100% {
    fill: #fff;
  }
}

video {
  z-index: -100;
  min-width: 100%;
  min-height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
